<template>
  <div class="github-chart" >
    <div class="svg-container" :style="{ width: `${w}px`, height: `${h}px` }">
      <svg id="calendar-graph"
        :width="w"
        :height="h"
        @mouseover="mouseover">
      </svg>
      <div class="svg-tip"
        :style="tooltipStyle"
        v-show="tooltipStyle.show" v-html="tooltipText">
      </div>
    </div>
  </div>
</template>

<script>
  import getYear from 'date-fns/get_year'
  import endOfYear from 'date-fns/end_of_year'
  import startOfYear from 'date-fns/start_of_year'
  import getDay from 'date-fns/get_day'
  import isBefore from 'date-fns/is_before'
  import addDays from 'date-fns/add_days'
  import format from 'date-fns/format'

  let d3 = Object.assign({}, require('d3-axis'), require('d3-selection'), require('d3-scale'))

  export default {
    name: 'vueContributionsChart',

    props: {
      year: {
        type: [ String ],
        default: getYear(new Date()) + ''
      },
      // 网格大小
      cellSize: {
        type: Number,
        default: 14
      },
      // 画布大小
      width: {
        type: Number,
        default: 0
      },
      height: {
        type: Number,
        default: 0
      },
      // 距离画布边框距离
      padding: {
        type: Number,
        default: 50
      },
      // 网格之间间距
      interval: {
        type: Number,
        default: 1
      },
      // 左列文字大小
      textFontsize: {
        type: Number,
        default: 10,
      },
      contributions: {
        type: Array,
        default: []
      }
    },
    data () {
      return {
        whichOfWeek: 0, // 星期几
        tooltipStyle: {
          position: 'absolute',
          top: '0px',
          left: '0px',
          show: false
        },
        tooltipText: ''
      }
    },
    computed: {
      w () {
        return this.width > 0 ? this.width : (this.cellSize + this.interval) * 54 + 100
      },
      h () {
        return this.height > 0 ? this.height : (this.cellSize + this.interval) * 7 + 20
      }
    },
    methods: {
      mouseoverHandler ($event) {
        $event.preventDefault()
        $event.target.removeEventListener('mouseleave', this.mouseoverHandler)
        this.tooltipStyle.show = false
      },
      mouseover ($event) {
        const { target } = $event
        target.addEventListener('mouseleave', this.mouseoverHandler)
        if (target.tagName.toLowerCase() === 'rect') {
          let x = +target.getAttribute('x')
          let y = +target.getAttribute('y')
          this.tooltipText = `
              <strong style="color: #dfe2e5">${target.dataset.value} contributions</strong> 
              on ${format(target.dataset.date, 'MMM Do YYYY')}
            `
          Object.assign(this.tooltipStyle, {
            show: true,
            top: `${y - this.cellSize * 2}px`,
            left: `${x - this.padding}px`
          })
        }
      },
      renderGraph () {
        const { cellSize, padding, interval, textFontsize } = this
        const distance = cellSize + 2 * interval
        let days = this.generateDays()
        let main = d3.select('#calendar-graph')
                    .append('g')
                    .attr('transform', `translate(${padding}, ${cellSize})`)
                    .selectAll('g')
                    .data(['Mon', 'Wed', 'Fri'])
                    .enter()
        
        main.append('text')
          .attr('transform', (_, i) => {
            return `translate(-${padding / 2}, ${distance * (2 * i + 1) + textFontsize})`
          })
          .style('font-size', `${textFontsize}px`)
          .style('text-anchor', 'start')
          .text((d) => d)

        let rect = main.selectAll('rect').data(days)
        let color = d3.scaleQuantile().domain([0, 1, 5, 10, 20])
                      .range([
                        '#eee', '#c6e48b',
                        '#7bc96f', '#239a3b',
                        '#196127'
                      ])

        rect
          .enter().append('rect')
          .attr('height', cellSize)
          .attr('width', cellSize)
          .attr('data-date', (d) => d.date)
          .attr('data-value', (d) => d.value)
          .attr('fill', (d) => { return color(d.value) })
          .attr('y', (_, i) => {
            let remains = (i + 1 + this.whichOfWeek) % 7
            let result = remains === 0 ? 6 : remains - 1
            return result * distance
          })
          .attr('x', (_, i) => { return ~~((i + this.whichOfWeek) / 7) * distance })
      },
      generateDays () {
        // 一年的第一天和最后一天
        let today = new Date(this.year)
        let date = startOfYear(today)
        let beginDate = startOfYear(today)
        this.whichOfWeek = getDay(beginDate) // 周几差值就是几 0是周日
        let days = []
        let endDate = endOfYear(today)
        while (isBefore(date, endDate)) {
          let target = this.contributions.find((item) => {
            return item.date === format(date, 'YYYY-MM-DD')
          })
          days.push(target || {
            date: format(date, 'YYYY-MM-DD'),
            value: 0
          })
          date = addDays(date, 1)
        }
        return days
      }
    },
    mounted () {
      this.renderGraph()
    }
  }
</script>

<style scoped>
.github-chart {
  background: #fff;
}
.svg-container {
  position: relative;
  display: inline-block;
}
.svg-tip {
  padding: 10px;
  width: 200px;
  background-color: rgba(0, 0, 0, .8);
  font-size: 12px;
  font-weight: 500;
  color: #959da5;
  text-align: center;
  border-radius: 3px;
  z-index: 10000;
}
</style>



